.product-card {
  background: $whiteColor;
  border: 1px solid $lightColor;
  border-radius: 5px;
  padding: 15px 14px;
  width: 200px;

  &__photo {
    width: 166px;
    height: 164px;
    margin: 0 auto;
    margin-bottom: 30px;
    padding-top: 12px;
    padding-bottom: 12px;
    transition: transform 1s, border-color 300ms;
    object-fit: contain;
    box-sizing: border-box;
    border: 3px solid transparent;
    border-radius: 5px;

    &:hover {
      border-color: $lightColor;
      transform: scale(1.2);
      cursor: pointer;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin-bottom: 5px;
  }

  &__info {
    font-size: 11px;
    line-height: 13px;
    margin-bottom: 15px;
  }

  &__buy {
    display: block;
    text-decoration: none;
    font: inherit;
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    color: $whiteColor;
    background-color: $hoverColor;
    border-radius: 5px;
    padding-top: 10px;
    padding-bottom: 11px;
    padding-left: 12px;
    padding-right: 12px;
    border: 1px solid transparent;
    transition: color 300ms, background-color 300ms, border-color 300ms;

    &:hover {
      color: $hoverColor;
      background-color: $whiteColor;
      border-color: $hoverColor;
    }
  }

  &__rating {
    margin-bottom: 20px;
  }

  &__price {
    margin-bottom: 20px;
  }
}
